<script>
import { filterName, filterTag } from '@/core/utils'

export default {
  name: 'SearchItem',
  props: {
    dataList: {
      site_title: '',
      book_name: '',
      movie_name: '',
      site_name: ''
    }
  },
  methods: {
    removeName (text) {
      return filterName(text)
    },
    removeTag (text) {
      return filterTag(text)
    }
  }
}
