<template>
  <div class="header">
    <div class="top-bar">
      <div class="logo">
        <a href="/">
          <img src="../../assets/img/lzpan.svg" alt="lzpan">
        </a>
      </div>
      <div class="search" ref="search">
        <input type="text" @keyup.enter="onSearch" aria-label="" placeholder="影视、小说、网盘"
               @focus="showSuggest=true"
               ref="input"
               v-model="kw">
        <button @click="onSearch"><span class="iconfont icon-search"></span></button>
        <div class="search-notice-box" v-if="suggestions.length > 0 && showSuggest" ref="suggestion-box">
          <div :class="{item:true,active: i === suggestionChose}" v-for="(item,i) in suggestions" :key="i"
               @click="choseKw(item)">
            {{ item }}
          </div>
        </div>
      </div>
      <div class="info"></div>
    </div>
    <div class="bottom-bar container">
      <ul class="items">
        <li :class="{item:true,active:what==='movie'}" @click="onWhat('movie')">影视</li>
        <li :class="{item:true,active:what==='book'}" @click="onWhat('book')">小说</li>
        <li :class="{item:true,active:what==='disk'}" @click="onWhat('disk')">网盘</li>
      </ul>
    </div>
  </div>
</template>

<script>
// import eventBus from '@/core/eventBus'
// import { EVENT_SEARCH } from '@/core/constants'

import { throttle } from '@/core/func'

export default {
  name: 'Header',
  data () {
    return {
      kw: '',
      showSuggest: false,
      suggestions: [],
      reqSuggest: false,
      suggestionChose: -1,
      what: 'movie'
    }
  },
  methods: {
    choseKw (val) {
      this.kw = val
      this.onSearch()
    },
    getBDSuggestion () {
      if (!this.reqSuggest) {
        return
      }
      if (this.kw === '') {
        return
      }
      this.$jsonp('https://suggestion.baidu.com/su?cb=jsonpCallback&wd=' + this.kw).then((res) => {
        console.log(res)
      })
        .catch(() => {
          // console.log('jsonp err: ', err)
        })
    },
    onWhat (what) {
      if (what !== this.what) {
        this.what = what
      }
    },
    onSearch () {
      if (this.kw !== '') {
        // this.$store.commit('changeKw', this.kw) //
        // eventBus.$emit(EVENT_SEARCH)
        this.pushRoute()
        location.reload()
      }
    },
    getParam () {
      const k = this.$route.query.kw || ''
      if (k && k !== '') {
        this.kw = k
        // this.$store.commit('changeKw', k) //
      }
      const w = this.$route.query.what || ''
      if (w && w !== '') {
        this.what = w
      }
    },
    pushRoute () {
      this.$router.push({
        name: 'Search',
        query: {
          kw: this.kw,
          showSuggest: false,
          suggestions: [],
          what: this.what
        }
      })
    }
  },
  watch: {
    what () {
      // this.$store.commit('changeWhat', val)
      if (this.$route.query.kw === this.kw && this.what === this.$route.query.what) return // 为了避免重复提交
      this.pushRoute()
      location.reload()
    },
    kw (newV) {
      if (this.$route.query.kw === this.kw && this.what === this.$route.query.what) return // 为了避免重复提交
      this.kw = newV
      throttle(this.getBDSuggestion, 100)()
      this.pushRoute()
    }
  },
  created () {
    this.getParam()
  },
  mounted () {
    this.$nextTick(() => {
      window.jsonpCallback = (res) => {
        this.suggestions = res.s
      }

      document.addEventListener('click', (e) => {
        if (e.target !== this.$refs['suggestion-box'] && e.target !== this.$refs.input) {
          this.showSuggest = false
        }
      })

      // 监听箭头选择
      this.$refs.input.addEventListener('keyup', (e) => {
        const code = e.keyCode
        if (code !== 40 && code !== 38) {
          this.reqSuggest = true
          return
        }

        this.reqSuggest = false
        if (code === 40) { // down
          this.suggestionChose = (this.suggestionChose + 1) % (this.suggestions.length || 0)
        }

        if (code === 38) { // up
          this.suggestionChose = (this.suggestionChose - 1) % (this.suggestions.length || 0)
          if (this.suggestionChose < 0) {
            this.suggestionChose = this.suggestions.length - 1
          }
        }

        this.kw = this.suggestions[this.suggestionChose]
      })
    })
  }
}
</script>

