import http from './http'

const reqSearch = (type, kw, page, size) => {
  return http.get(`/search/v1?what=${type}&kw=${kw}&page=${page}&size=${size}`)
}

const reqRelativeBook = (name) => {
  return http.get(`/official_book/v1/search?name=${name}`)
}
const reqBanDisk = (val) => {
  return http.post('/disk/v1/ban', { ...val })
}

export {
  reqSearch,
  reqBanDisk,
  reqRelativeBook
}
