<template>
  <div class="latest-book">
    <div class="title">最新小说</div>
    <div class="list">
      <div class="item" v-for="(item,i) in dataList" :key="i">
        <div
          :title="item.desc"
          class="cnt"><a target="_blank" :href="item.link">{{ removeTag(item.book_name) }} <span class="author">({{ item.author }})</span></a></div>
      </div>
    </div>
  </div>
</template>

<script>
import { reqLatestBook } from '@/api/book'
import { removeBookNameTag } from '@/core/utils'

export default {
  name: 'LatestBook',
  data () {
    return {
      dataList: []
    }
  },
  methods: {
    removeTag (t) {
      return removeBookNameTag(t)
    },
    toList () {
      reqLatestBook(10)
        .then((res) => {
          if (res.code === 200) {
            this.dataList = res.data
          }
        })
    }
  },
  created () {
    this.toList()
  }
}
</script>

