<template>
  <div>
    <div class="item" v-for="(item,i) in dataList" :key="i" :title="removeTag(item.disk_name)">
      <a href="#" @click.prevent="jump(getLink(item.disk_type,item.disk_id))" target="_blank">
        <h3 class="title" v-html="getTitle(item.disk_name, item.disk_user,item.disk_type)"></h3>
      </a>
      <div class="info" v-if="item.disk_info" v-html="getInfo(item.disk_info)"></div>
      <span class="tag"><i class="iconfont icon-attachent"></i>{{ item.disk_type }}</span>
      <span class="tag"><i class="iconfont icon-usercenter"></i>{{ item.disk_user }}</span>
      <span class="tag ban"
            @click="toBan(item.disk_type,item.disk_id)"
      ><i class="iconfont icon-ban3"></i>违规删除</span>
      <span class="tag pwd"
            data-clipboard-action="copy"
            :data-clipboard-text="item.disk_pass"
            v-if="item.disk_pass !== '' && item.disk_pass !== null"><i
        class="iconfont icon-password"></i>{{ item.disk_pass }}</span>
      <!--            <span class="tag">2020-10-02</span>-->
    </div>
  </div>
</template>

<script>
import Clipboard from 'clipboard'
import { TEXT_COPY_FAIL, TEXT_COPY_SUCCESS } from '@/core/constants'
import { filterTag } from '@/core/utils'
import { getLink, jumpAd } from '@/core/helper'

export default {
  name: 'SearchDiskItem',
  data () {
    return {
      notice: false,
      clip: null
    }
  },
  props: {
    dataList: {
      disk_name: '',
      disk_id: '',
      disk_type: '',
      disk_info: '',
      disk_user: '',
      disk_pass: ''
    }
  },
  methods: {
    jump (url) {
      jumpAd(url)
    },
    toBan (diskType, diskID) {
      const url = this.$router.resolve({
        name: 'Ban',
        query: {
          diskType,
          diskID
        }
      })
      window.open(url.href, '_blank')
    },
    getInfo (info) {
      if (typeof info !== 'string') {
        return info
      }

      let res = ''
      const str = info.split('\n')
      for (let i = 0; i < str.length; i++) {
        if (i >= 10) {
          res += '<br />' + '....'
          break
        }
        if (i === 0) {
          res = str[i]
        } else {
          res += '<br />' + str[i]
        }
      }
      return res
    },
    removeTag (text) {
      return filterTag(text)
    },
    getTitle (diskName, diskUser, diskType) {
      return diskName + ' - ' + diskUser + '分享' + this.getDiskBelong(diskType)
    },
    getDiskBelong (diskType) {
      switch (diskType) {
        case 'LZY':
          return ' - 蓝奏云'
        case 'BDY':
          return ' - 百度云'
        case 'TYY':
          return ' - 天翼云'
      }
      return ''
    },
    getLink (type, diskID) {
      return getLink(type, diskID)
    }
  },
  mounted () {
    this.clip = new Clipboard('.tag.pwd')
    this.clip.on('success', () => {
      this.$notify.success(TEXT_COPY_SUCCESS)
    })
    this.clip.on('error', () => {
      this.$notify.error(TEXT_COPY_FAIL)
    })
  }
}
</script>

