<template>
  <div class="main container">
    <div class="search-info">为您搜索到:<span class="num" v-text="total"/>条结果</div>
    <div class="cnt">
      <div class="left-list">
        <not-found :length="dataList.length" :searched="searched"/>
        <search-item :data-list="dataList" v-if="what === 'movie' || what === 'book'"/>
        <search-disk-item :data-list="dataList" v-if="what === 'disk'"/>
        <infinite-loading @infinite="toSearch"></infinite-loading>
      </div>

      <div class="right-info">
        <div class="sticky">
          <!--          <d-b-info/>-->
          <book-info :book="dataBook" v-if="!isEmpty(dataBook)"/>
          <latest-book/>
          <div class="copyright">
            <span>&copy;{{ year }} Lzpan
              <span><a href="#" @click.prevent="toAbout">About</a></span>
            </span>
          </div>
          <friend-link/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import eventBus from '@/core/eventBus'
// import { EVENT_SEARCH, KEY_BOOK_TAG, MAX_RESULT_PAGE } from '@/core/constants'
import { reqRelativeBook, reqSearch } from '@/api/search'
import InfiniteLoading from 'vue-infinite-loading'

import { filterName, filterTag } from '@/core/utils'
import NotFound from '@/components/search/item/NotFound'
// import DBInfo from '@/components/search/item/DBInfo'
import FriendLink from '@/components/search/item/FriendLink'
import BookInfo from '@/components/search/item/BookInfo'
import LatestBook from '@/components/search/item/LatestBook'
import { KEY_BOOK_TAG, MAX_RESULT_PAGE } from '@/core/constants'
import SearchItem from '@/components/search/item/SearchItem'
import SearchDiskItem from '@/components/search/item/SearchDiskItem'

export default {
  name: 'Main',
  components: {
    SearchDiskItem,
    SearchItem,
    LatestBook,
    BookInfo,
    FriendLink,
    // DBInfo,
    NotFound,
    InfiniteLoading
  },
  data () {
    return {
      year: '',
      what: 'book',
      kw: '',
      relation: false, // 相关的官方推荐
      // changedWhat: false,
      page: 0,
      size: 20,
      total: 0,
      searched: false,
      dataList: [],
      dataBook: {}
    }
  },
  methods: {
    isEmpty (obj) {
      return (JSON.stringify(obj) === '{}')
    },
    removeTag (text) {
      return filterTag(text)
    },
    removeName (name) {
      return filterName(name)
    },

    toAbout () {
      this.$router.push({ name: 'About' })
    },
    toSearch ($state) {
      this.$progress.start()

      this.page++

      // 重置计数器
      // if (this.changedWhat) {
      //   $state && $state.reset()
      //   this.changedWhat = false
      // }

      reqSearch(this.what, this.kw, this.page, this.size)
        .then((res) => {
          if (res.code === 200) {
            this.total = res.data.total
            this.dataList.push(...res.data.result)
            if (this.total === 0) {
              $state && $state.complete()
            } else {
              $state && $state.loaded()
            }
          }
          this.searched = true

          // 官方书籍结果
          if (this.what === KEY_BOOK_TAG && !this.relation) {
            if (this.dataList.length > 0) {
              const bookName = this.dataList[0].book_name
              this.toBook(this.removeName(bookName))
              this.relation = true
            }
          }
        }).finally(() => {
          this.$progress.done()
          if (this.page * this.size >= this.total || this.page >= MAX_RESULT_PAGE) {
            $state && $state.complete()
          }
        })
    },
    // 正版小说信息展示
    toBook (name) {
      reqRelativeBook(name)
        .then((res) => {
          if (res.code === 200) {
            this.dataBook = res.data
          }
        })
    },
    // 初始化参数
    initParam () {
      // this.changedWhat = true
      this.searched = false
      this.dataList = []
      this.dataBook = {}
      this.page = 0
    }
  },
  created () {
    const what = this.$route.query.what || ''
    if (what !== '') {
      this.what = what
    }

    const kw = this.$route.query.kw || ''
    if (kw !== '') {
      this.kw = kw
      this.initParam()
      // this.toSearch()
    }

    this.year = new Date().getFullYear()
  },
  watch: {
    // what () { // 状态改变了
    //   this.initParam()
    //   console.log('watch what' + this.what)
    //   if (this.kw !== '') {
    //     this.toSearch()
    //   }
    // },
    // '$store.state.kw' (val) {
    //   this.kw = val
    //   if (val !== '') {
    //     this.initParam()
    //     this.toSearch()
    //   }
    // }
  },
  computed: {
    // what () {
    //   return this.$store.state.what
    // }
  }
}
</script>

