import { jumpUrl } from '@/core/utils'
import { MALL_URL } from '@/core/constants'

const sKey = 'a'
const sVal = 'o'
export const jumpAd = (url) => {
  const item = sessionStorage.getItem(sKey) || ''
  if (item === '') {
    sessionStorage.setItem(sKey, sVal)
    jumpUrl(MALL_URL)
  } else {
    jumpUrl(url)
  }
}

export const getLink = (diskType, diskID) => {
  diskType = diskType.toUpperCase()
  switch (diskType) {
    case 'LZY':
      return 'https://lanzous.com/' + diskID
    case 'BDY':
      return 'https://pan.baidu.com/s/1' + diskID
  }
  return '#'
}
